@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,400;0,500;0,700;1,300;1,400;1,500;1,700&family=Offside&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}
body {
  margin: 0;
}
article,
aside,
footer,
header,
nav,
section {
  display: block;
}
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}
figcaption,
figure,
main {
  display: block;
}
figure {
  margin: 1em 40px;
}
hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}
pre {
  font-family: monospace, monospace;
  font-size: 1em;
}
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}
abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
          text-decoration: underline dotted;
}
b,
strong {
  font-weight: inherit;
}
b,
strong {
  font-weight: bolder;
}
code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
dfn {
  font-style: italic;
}
mark {
  background-color: #ff0;
  color: #000;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio,
video {
  display: inline-block;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
img {
  border-style: none;
}
svg:not(:root) {
  overflow: hidden;
}
button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}
button,
input {
  overflow: visible;
}
button,
select {
  text-transform: none;
}
button,
html [type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}
fieldset {
  padding: 0.35em 0.75em 0.625em;
}
legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
textarea {
  overflow: auto;
  resize: none;
}
[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
details,
menu {
  display: block;
}
summary {
  display: list-item;
}
canvas {
  display: inline-block;
}
template {
  display: none;
}
[hidden] {
  display: none;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: "";
  clear: both;
  height: 0;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.srOnly {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

/* Stripping padding and margin */
* {
  padding: 0;
  margin: 0;
}

/* Make fontsizes a multiple of ten */
html {
  font-size: 62.5%;
}

img {
  height: auto;
  display: block;
}

/* Wrapper to constrain content on large sceens */
.wrapper {
  /* max-width: 90%; */
  width: 100%;
  margin: 0 auto;
}

/* Google Webfont Import */

/* COLORS FROM DESIGNER */
/* dark green: #073330 */
/* light green:#b1bc2b */
/* blue: #416bff */

/* font-family: 'Barlow', sans-serif;
font-family: 'Offside', cursive; */

header {
  background-image: url(/clonedCollegeNav/static/media/header.dacb15c2.jpg);
  background-size: cover;
  min-height: 40vh;
  background-position: left;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 95vh;
}

.buttonNav {
  color: #073330;
  padding: 10px 20px;
}

.buttonNav:active {
  color: black;
}

h1 {
  font-family: "Offside", cursive;
  color: #073330;
  font-size: 7rem;
  margin-bottom: 10%;
  background: #416aff59;
  padding: 20px;
  text-shadow: 2px 2px 2px #b1bc2b;
  padding-left: 5%;
}

h2 {
  margin-top: 20px;
  text-align: center;
}

body {
  font-family: "Barlow", sans-serif;
  font-size: 1.8rem;
  /* background-color: rgb(200, 229, 255); */
  background-image: url(/clonedCollegeNav/static/media/pexels-johannes-plenio-1103970.b9a92a4f.jpg);
  background-size: cover;
}

form {
  background-color: #073330bb;
  color: white;
  padding: 20px;
  display: flex;
  flex-direction: column;
  font-weight: 500;
}

.cityInputs,
.radiusInputs,
.schoolTypeInputs {
  margin: 15px 0;
}

.radiusInputs div,
.schoolTypeInputs div {
  display: inline;
}

input[type="text"] {
  margin: 0 5px;
  width: 300px;
  padding: 10px;
}

input[type="radio"] {
  margin: 10px 20px;
}

header button,
.addSchoolButton {
  width: 20%;
  padding: 10px;
  background-color: white;
  color: #073330;
  font-weight: 700;
  border: #b1bc2b;
  /* min-width: 200px; */
}

nav {
  background-color: #b1bc2b;
  border-bottom: 2px solid #073330;
  padding: 15px;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: space-evenly;
}

a {
  text-decoration: none;
  font-weight: 700;
  color: #416bff;
}

details {
  margin: 20px;
}

details p {
  display: flex;
  flex-direction: column;
  margin: 10px;
  color: #073330;
}

summary {
  color: #073330;
  font-weight: 700;
}

summary:hover {
  cursor: pointer;
}

summary:focus {
  border-bottom: 2px solid #073330;
}

.searchLocation {
  border-bottom: 2px solid #b1bc2b;
  font-size: 3rem;
}

.schoolResults button {
  margin: 10px;
  margin-left: 8%;
  padding: 5px;
  background-color: #073330;
  color: white;
  border: none;
  width: 100px;
  font-size: 1.3rem;
  font-weight: 600;
}

.searchedSchools button {
  width: 100px;
}

.schoolResults {
  border: 2px solid #b1bc2b;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  background-color: white;
}

.schoolResults button {
  padding: 10px 0;
}

.addedSchool button {
  margin-left: 10px;
}

.addedSchool,
.searchedSchools {
  width: 100%;
}

.allResults {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* max-width: 600px; */
  max-height: 100vh;
  overflow: scroll;
}

.resultsScrollBox {
  display: flex;
  flex-direction: column;
}

.combinedSchools {
  display: grid;
  grid-template-columns: 1fr 3fr;
  grid-gap: 10px;
}

.mapHeading {
  height: 90vh;
  width: auto;
  position: relative;
  margin-right: 10px;
}

.mapContainer {
  position: absolute;
  height: 100%;
  width: 100%;
  max-height: 900px;
  /* top: 0;
  right: 0;
  left: 0;
  bottom: 0; */
  margin-top: 10px;
  border: 2px solid #b1bc2b;
  /* overflow: auto; */
}

.mapboxgl-popup {
  max-width: 200px;
}




.favoritesFlex {
  display: flex;
  flex-direction: column;
  margin: 20px;
  border: 2px solid #b1bc2b;
  width: 700px;
  padding-left: 10px;
  background-color: white;
}

.favoritesFlex button {
  margin: 10px 0;
  background-color: #073330;
  color: white;
  font-weight: 600;
  padding: 10px 0;
  border: none;
  width: 40%;
  margin-right: 5px;
}

.favoritesFlex h3 {
  margin: 10px 0;
}

.favoritesFlex p {
  margin: 5px 0;
}

.favoritesFlex textarea {
  margin-top: 10px;
}

.favorites li:last-of-type {
  display: none;
}

.buttonFlex {
  display: flex;
}

textarea {
  width: calc(80% + 5px);
}

.addressInputs {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.addressInput {
  display: flex;
  flex-direction: column;
}

.addSchoolButton {
  margin: 10px 0;
}

.addSchoolForm {
  margin: 2% 0px;
}

.addSchoolForm input[type="text"] {
  width: auto;
  width: initial;
}

.searchedSchools span {
  text-transform: capitalize;
}

/* Footer */
footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #416bff;
  font-weight: 500;
  padding: 10px;
}

footer p {
  margin-bottom: 15px;
}

footer a {
  color: white;
}

footer a:hover,
footer a:hover {
  border-bottom: 2px solid #073330;
}

/* hover Focus */

button:hover,
button:focus {
  background-color: #416bff;
  color: white;
}

a:hover,
a:focus {
  color: white;
}


@media (max-width: 980px) {
  

  .combinedSchools {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
  }

  .favoritesFlex {
    width: 80%;
  }

  .schoolResults {
    margin: 10px auto;
  }

  .schoolResults summary {
    text-align: left;
  }

  .searchedSchools {
    text-align: center;
  }
}

@media (max-width: 800px) {
  header {
    height: auto;
  }

  h1 {
    font-size: 8rem;
  }

  nav ul {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .schoolTypeInputs div {
    display: block;
  }

  nav li {
    margin: 10px 0px;
  }

  .addressInputs {
    display: flex;
    flex-direction: column;
  }

  .cityInputs {
    width: 60%;
  }

  .cityInputs input:first-child {
    margin-bottom: 15px;
  }

  footer span {
    display: block;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 4rem;
  }

  nav ul {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  nav li {
    margin: 10px 0px;
  }

  .radiusInputs div {
    display: block;
  }

  .schoolResults {
    min-width: 300px;
    margin-bottom: 30px;
  }

  .addedSchool .schoolResults {
    min-width: 200px;
  }

  .combinedSchools {
    display: flex;
    flex-direction: column;
    justify-items: center;
  }
}

